.mobiles{
    width: 100%;
    position: relative;
   &-choosing{
      width: 100%;
      text-align: center;
      h2{
         padding-bottom: 60px;
      }
   }

    &-offers{
        
        position: relative;
        width: 100%;
        user-select: none;
        h1{
            text-align: center;
            margin: 10px 0;
        }
        &--list{
            width:1200px;
            margin: 0 auto;
            height: 450px;
            cursor: pointer;
            
            ul{
                display: flex;
                width: 1200px;
                list-style-type: none;
            }
            .li1{
                width: 1200px;
            }
            li{
                behavior: scroll;
                border-left: 1px solid black;
                transition: all 0.5s;
                overflow: hidden;
               
                .title{
                    background-color: rgba(0, 0, 0, 0.3);
                    height: 100px;
                    @include flex(space-around,center);
                    width:100%;
                    font-size: var(--font_small);
                    color: var(--simple_color);
                    
                    a{
                        color: var(--simple_color);
                        text-decoration: none;
                        font-size: var(--font_medium);  
                   
                    }
                    .storelogo{
                        
                        font-size: 40px;
                        
                    }
                    &>div *{
                        display: block;
                       padding: 5px 0;
                        
                    }
                    &>div i{
                            text-decoration: line-through rgb(255, 255, 255) solid ;
                            color: rgba(255, 255, 255, 0.568);
                            
                            
                        }
                    & > p{
                        background-color: red;
                        border-radius: 40px;
                        padding: 8px 16px;
                        font-size: 10px;
                    }
            }
           
            img{
                    width: 100%;
                    height: 350px;
            }    
                
            }
            
            
            
           
        }
        & > i{
            position: absolute;
            top: 250px;
            color: var(--simple_color);
            z-index: 99;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            cursor: pointer;
            background-color: var(--secondary_color);
            line-height: 80px;
            text-align: center;
            
            border: 2px solid rgba(255, 255, 255, 0.518);
            opacity: 0.4;
            
           }
           .left{
               left: 50px;
               
           }
           .right{
               right: 50px;
           }
           i:hover{
            opacity: 0.6;
           }
           i:active{
            opacity: 0.8;
           }
    }
}

.goUp{
    position: fixed;
            bottom: 50px;
            right: 50px;
            color: var(--simple_color);
            z-index: 99;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            cursor: pointer;
            color: var(--secondary_color);
            background-color: var(--simple_color);
            font-size: var(--font_small);
            font-weight: bold;
            line-height: 80px;
            text-align: center;
            user-select: none;
            border: 2px solid rgba(255, 255, 255, 0.518);
            opacity: 0.6;
            &:hover{
                opacity: 0.8;
               }
            &:active{
                opacity: 0.7;
               }
}